.profile-links {
    position: fixed;
    left: 1%;
    top: 8%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.click{
    cursor: pointer;
}

.fa-brands {
    color: #eee;
    font-size: 3em;
